import React from 'react';
import { NavLink, useLocation } from 'react-router-dom';

export const NavItem = ({ title, link, exact, headerItemRef }: NavItemProps) => {
  const location = useLocation();
  return (
    <li className="nav__item">
      <NavLink
        to={link}
        ref={location.pathname === link ? headerItemRef : null}
        exact={exact}
        className="nav__link"
      >
        {title}
      </NavLink>
    </li>
  );
};
